import React, { Component } from 'react';
import './App.css';
import Welcome from './components/Welcome'
import Header from './components/Header'
import {Container, Button, TextField} from '@material-ui/core/'

import Weather from './components/Weather'
class App extends Component{

  state = {

    userLogin: false, //user isnt login when page load
    username: null
 }

 btnLogin = () => {
   const getUserLogin = this.state.userLogin
  this.setState(
    {
      userLogin: !getUserLogin
    }
  )
}

changeInputText = (event) => {
  this.setState({
    username: event.target.value

  })
}


render(){
  return(
  <div>
    
     <Header></Header>
     <Container maxWidth="lg">





       
    {
      this.state.userLogin ?
      <div>
       
       <Welcome username={this.state.username}> </Welcome>

       <Weather></Weather>
        <br />
        <Button variant="contained" color="secondary" onClick={this.btnLogin}>
        Logout
      </Button>

      </div>
      : 


      <div>
         <TextField id="standard-basic" label="Enter Username" onChange={this.changeInputText}/>
    
<br/>
<br/>
      <Button variant="contained" color="primary" onClick={this.btnLogin}>
        Login
      </Button>

      
      <br/>
        <p>Please login to continue</p>
      </div>

    }
    </Container>
  </div>

  )}}

export default App
